<template>
    <div class="playlist-item" @click="getDetail(playListId)">
        <div class="item_container" >
            <a href="">
                <img v-lazy="picUrl">
            </a>
            <div class="name">{{name}}</div>
        </div>
        <div class="mask">
            <div class="count">播放量 {{playCount}}</div>
        </div>
    </div>
</template>
<script>
export default {
  name: "playlistItem",
  props: {
    // 歌单 id
    playListId: Number,
    // 歌单图片
    picUrl: String,
    // 歌单名字
    name: String,
    // 播放量
    playCount: String,
  },
  watch: {
  },
  methods: {
    getDetail(id) {
      this.$emit("playlistDetail", id);
    },
  },
};
</script>
<style lang="scss">
@import "../../assets/scss/music.scss";
.playlist-item {
  position: relative;
  margin-bottom: 20px;
  border-radius: 12px;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.3);
  .item_container {
    width: 224px;
    height: 283px;
    a {
      display: block;
      height: 224px;
      overflow: hidden;
    }
    img {
      display: block;
      height: 100%;
    }
    .name {
      padding: 10px;
      box-sizing: border-box;
      @include font(13px, 20px, $colorC);
      @include fontHidden();
    }
  }
  .mask {
    @include position();
    @include setMask();
    .count {
      height: 40px;
      box-sizing: border-box;
      padding-left: 12px;
      background-color: rgba(0, 0, 0, 0.3);
      @include font(14px, 40px, $colorD);
    }
  }
  &:hover {
    z-index: 2;
    box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.3);
    transform: translate3d(0, -2px, 0);
    .mask {
      opacity: 1;
    }
  }
}
</style>