<template>
  <div class="play-list">
    <div class="music-container">
      <div class="tab">
        <span
          :class="['item',type==index? 'active': '']"
          v-for="(item, index) in tab"
          :key="index"
          @click="getPlayList(index)"
        >{{item}}</span>
      </div>
      <div class="playlist-content">
        <play-list-item
          class="item"
          v-for="(item, index) in playLists"
          :key="index"
          :playListId="item.id"
          :picUrl="item.coverImgUrl"
          :name="item.name"
          :playCount="item.playCount"
          @playlistDetail="goplaylistDetail(item.id)"
        >
          </play-list-item>
      </div>
      <div
        class="pagination"
        v-if="paginationShow"
      >
        <el-pagination
          :background="true"
          layout="total, prev, pager, next ,jumper"
          :page-size="playListNum"
          :total="total"
          @current-change="handleCurrentChange"
        >
          </el-pagination>
    </div>

  </div>
  </div>
</template>
<script>
import { req_update_playList_Tab } from "../network/song/index.js";
import tools from "../tools/formatData.js";
import playListItem from "../components/item/playListItem";
export default {
  name: "PlayList",
  components: {
    playListItem,
  },
  data() {
    return {
      type: 0,
      paginationShow: false,

      playLists: [],
      playListNum: 50,
      playListPage: 0,
      total: 0,

      tab: [
        "全部",
        "华语",
        "流行",
        "摇滚",
        "民谣",
        "电子",
        "另类/独立",
        "轻音乐",
        "综艺",
        "影视原声",
        "ACG",
      ],
    };
  },
  watch: {
    playLists: function() {
      this.playLists.forEach((e, index, arr) => {
        let value = tools._tranNumber(e.playCount, 2);
        arr[index].playCount = value;
      });
      this.paginationShow = true;
    },
  },
  methods: {
    // 获取歌单
    getPlayList(type = this.type) {
      this.type = type;
      document.documentElement.scrollTop = 0;
      let offset;
      if (this.playListPage == 0) {
        // 排除掉页面第一次请求
        offset = 0;
      } else {
        offset = this.playListNum * (this.playListPage - 1);
      }
      let params = {
        limit: 50,
        cat: this.tab[this.type],
        offset,
      };
      req_update_playList_Tab(this, params)
        .then(res => {
          this.total = res.total;
          this.playLists = res.playlists;
          this.playListPage++;
        })
        .catch(error => {
          console.log(error);
        });
    },
    // 跳转到歌单详情
    goplaylistDetail(id) {
      let time = new Date().getTime();
      // 跳转到歌单页面
      this.$router.push({
        path: `/playListDetail/${time}`,
        query: { id },
      });
    },
    // 监听 页码值 改变的事件
    handleCurrentChange(newPage) {
      //把最新的页码（newPage）赋值给 动态的 commentPage
      this.playListPage = newPage;
      //获取到最新显示的页码值  重新发送axios请求
      this.getPlayList();
    },
  },
  created() {
    this.getPlayList();
  },
};
</script>
<style lang="scss">
@import "../assets/scss/music.scss";
.play-list {
  .music-container {
    .tab {
      @include flex(flex-start);
      margin: 12px 0px;
      padding-bottom: 12px;
      border-bottom: 1px solid rgba($color: #000000, $alpha: 0.4);
      .item {
        display: block;
        padding: 10px, 12px;
        margin-right: 12px;
        text-align: center;
        @include font(16px, 20px, $colorB);
        &:hover {
          color: $colorA;
          cursor: pointer;
        }
      }
      .active {
        color: $colorA;
      }
    }
    .playlist-content {
      @include flex();
      flex-wrap: wrap;
    }
    .pagination {
      @include flex(center);
      margin-top: 20px;
      // 修改分页器的颜色
      .el-pagination.is-background .el-pager li:not(.disabled).active {
        background-color: #31c27c;
        color: #fff;
      }
    }
  }
}
</style>